.developpement-container {
    display: grid;
    align-items: center;
    justify-content: left;
    width: 100%;
    height: 100%;

    .texte {
        font-size: 1.4rem;
        text-align: left;
        margin-top: 20px;
        margin-left: 15px;
    }
}

.modal-virtu {
    color: $white;
    background: $black;
    font-size: 1.5rem;
    width: 100vh;
    height: 300px;
    position: relative;
    top: 50px;
    padding: 10px;
    border: 1px solid $white;
    box-shadow: 10px 10px 5px $white;
    text-justify: distribute;
    letter-spacing: 2px;
    border-radius: 20px;
    padding: 20px;
    animation: anim_texte 1s linear 0s 1 normal forwards;
}
.modal-dev {
    color: $white;
    background: $black;
    font-size: 1.1rem;
    width: 100vh;
    height: 450px;
    position: relative;
    top: 50px;
    padding: 10px;
    border: 1px solid $white;
    box-shadow: 10px 10px 5px $white;
    text-justify: distribute;
    letter-spacing: 2px;
    border-radius: 20px;
    padding: 20px;
    animation: anim_texte 1s linear 0s 1 normal forwards;
}

a.close {
    color: #b97629;
    position: relative;
    font-size: 2rem;
    cursor: pointer;
    &:hover {
        color: rgb(102, 10, 10);
    }

    text-align: right;
}

.button {
    color: $white;
    background-color: transparent;
    border: none;
    border-radius: 10px;
    padding: 5px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 1rem;

    cursor: pointer;
    &:hover {
        background: $grey;
    }
}

@keyframes anim_texte {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
