// Définir les couleurs de la palette
$bleu-atos: #0057a3; // Couleur du bleu du logo de l'entreprise ATOS[^1^][4]
$rose-exodata: #f9a8d4; // Couleur du rose de l'entreprise Exodata[^2^][1]
$bleu-rose-1: mix($bleu-atos, $rose-exodata, 25%); // Mélange de 25% de bleu et 75% de rose
$bleu-rose-2: mix($bleu-atos, $rose-exodata, 50%); // Mélange de 50% de bleu et 50% de rose
$bleu-rose-3: mix($bleu-atos, $rose-exodata, 75%); // Mélange de 75% de bleu et 25% de rose

.apropos {
    width: 100%;
    height: 100%;
    display: grid;
}
.image-profile {
    img {
        width: 150px;
        position: absolute;
        top: 25px;
        left: 25px;
        border-radius: 50%;
        border: 1px solid $white2;
        box-shadow: 10px 5px 5px #dfdfdf;
    }
}
.title {
    color: $white;
    margin-top: 50px;
    text-align: center;
    position: absolute;
    top: 0;
    left: 20vw;
}
.presentation {
    color: $white;
    margin-top: 100px;
    text-align: center;
    left: 20vw;
    font-size: 1rem;
    margin-bottom: 50px;
}
.list-hobbies {
    display: grid;
    grid-template-columns: repeat(28, 1fr);
    grid-auto-rows: minmax(75px, auto);
    font-size: 1rem;
    margin-left: 75px;
    margin-right: 75px;
    border: 1px solid $white2;
    text-align: center;
}

