@font-face {
    font-family: "Ubuntu";
    src: url(./fonts/Ubuntu-Medium.ttf);
}

$white: #e7e6e6;
$white2: #e7e6e69a;
$black: #1d1d1d;
$black2: #464646;
$grey: #808080;
$green: #0b440b;
$brown: #e29055;

* {
    box-sizing: border-box;
}

body {
    background-color: $black;
    color: $white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: "Ubuntu", sans-serif;
    overflow-x: hidden;

    .active {
        position: relative;

        &:before {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 3px;
            background-color: $white;
        }
    }

    section {
        display: grid;
        place-items: center;
        height: 100vh;

        font-size: 64px;
        font-weight: bold;
        position: relative;
        align-items: center;
    }
}
