.devnotes-container {
    height: 100%;
    width: 100%;
    color: $black;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    li {
        list-style-type: none;
    }
    .box-container {
        margin: 10px;
        background: transparent;
        border: 1px solid $black;
        background-color: $white;
        border-radius: 20px;
        padding: 10px;
        height: 170px;
        box-shadow: 5px 5px 5px $green;
    }
    .categorie-title {
        font-size: 1.5rem;
        font-weight: bold;
        margin-bottom: 10px;
        padding: 0;
        text-align: center;
        color: $green;
    }
    .skill {
        display: grid;
        grid-template-columns: 1fr 1fr;
        width: 80%;
    }
    .skill-title {
        font-size: 1.1rem;
        font-weight: bold;
        margin: 0;
        padding: 0;
    }
    .skill-stars {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        justify-items: center;
        align-items: center;
    }
    .faStarChecked {
        color: $green;
        padding: 1px;
    }
    .faStarUnchecked {
        color: $grey;
    }
}
