.home {
    background: url("../../public/background1.jpg") repeat center fixed;
    height: 100vh;
    width: 100vw;

    scroll-behavior: smooth;
    text-align: center;

    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .texte {
        color: $black;
        background-color: $white2;
        width: 80%;
        padding: 30px;
        animation: anim_texte 1s linear 0s 1 normal forwards;
        border-radius: 20px;
        border-style: solid;
    }
    .anglesdown {
        margin-top: 40vh;
        animation: anim_anglesdown 2s linear 0s 500 normal forwards;
        transition: 1s;

        a {
            color: rgb(231, 105, 105);
            :visited {
                color: none;
            }
        }
        a:hover {
            color: rgb(209, 15, 15);
            font-size: 5rem;
            animation: anim_texte 0.3s linear 0s 1 normal forwards;
        }
    }
}

@keyframes anim_texte {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes anim_anglesdown {
    0%,
    50%,
    100% {
        opacity: 0;
    }

    25%,
    75% {
        opacity: 1;
    }
}
