.skills-container {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 35% 1fr;
    grid-template-rows: 80% 1fr;
    justify-content: center;

    align-items: center;
    justify-content: space-between;
    background: url("../../public/img/background-competences2.jpg") no-repeat center;
    background-size: cover;

    .title-competence {
        color: $black;
        text-align: center;
        padding: 10px;
        background-color: $white;
        border-radius: 10px;
        width: 98%;
        font-size: 4rem;

        margin: 30px;
    }
    .card-container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        padding: 20px;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        margin-left: 30px;

        .flip-card {
            width: 300px;
            height: 200px;
            perspective: 1000px; /* Remove this if you don't want the 3D effect */

            margin-bottom: 60px;
        }

        /* This container is needed to position the front and back side */
        .flip-card-inner {
            position: relative;
            width: 100%;
            height: 100%;
            text-align: center;
            transition: transform 1.2s;
            transform-style: preserve-3d;
            background-color: $black;
            border-radius: 10px;
        }

        /* Do an horizontal flip when you move the mouse over the flip box container */
        .flip-card:hover .flip-card-inner {
            transform: rotateY(180deg);
        }

        /* Position the front and back side */
        .flip-card-front,
        .flip-card-back {
            position: absolute;
            width: 100%;
            height: 100%;
            -webkit-backface-visibility: hidden; /* Safari */
            backface-visibility: hidden;
            font-size: 2rem;
        }

        /* Style the front side (fallback if image is missing) */
        .flip-card-front {
            color: rgb(240, 240, 240);
            border: 1px solid $white2;
            border-radius: 10px;
            box-shadow: 10px 5px 5px #2980b9;
        }
        .imgcard {
            width: 100%;
            height: 100%;
            margin: 10px;
        }

        /* Style the back side */
        .flip-card-back {
            background-color: transparent;
            transform: rotateY(180deg);
            border: 1px solid $white2;
            border-radius: 10px;
            box-shadow: 10px 5px 5px #2980b9;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        .virtu {
            color: $white;
        }

        .one {
            grid-column: 1;
            grid-row: 1;
        }
        .two {
            grid-column: 2;
            grid-row: 1;
        }
        .three {
            grid-column: 3;
            grid-row: 1;
        }
        .four {
            grid-column: 1;
            grid-row: 2;
        }
        .five {
            grid-column: 2;
            grid-row: 2;
        }
        .six {
            grid-column: 3;
            grid-row: 2;
        }

        .card {
            font-size: 1.5rem;
            justify-content: center;
            margin: 10px;
            background: rgba(173, 173, 173, 0.842);
            border-radius: 4px;

            .card-title {
                font-weight: bold;
                color: $black;
                background-color: $white;
                transition: 0.5s;
                border-radius: 10px 10px 0 0;
                text-align: center;
            }
            .card-body {
                border-radius: 10px;
                ul {
                    list-style-type: none;
                }
                li {
                    cursor: default;
                }
                .skill-container {
                    width: 100%;
                    background-color: #ddd;
                    .skills {
                        text-align: right;
                        padding-top: 10px;
                        padding-bottom: 10px;
                        color: white;
                    }
                    .html {
                        width: 90%;
                        background-color: DarkOrange;
                    }
                    .css {
                        width: 80%;
                        background-color: DodgerBlue;
                    }
                    .js {
                        width: 65%;
                        background-color: Yellow;
                    }
                }
            }
        }
    }
    .anglesdown {
        animation: anim_anglesdown 2s linear 0s 500 normal forwards;
        text-align: center;
        a {
            color: $white;
        }
        a:visited {
            color: $white;
        }
    }
}
