.virtualisation-container {
    display: grid;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    .logos {
        padding: 20px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
        align-items: center;
        justify-content: center;
    }
}
.modal {
    color: $white;
    background: $black;
    font-size: 1.8rem;
    width: 100vh;
    height: 400px;
    position: relative;
    top: 50px;
    padding: 10px;
    border: 1px solid $white;
    box-shadow: 20px 20px 5px $white;
    text-justify: distribute;
    letter-spacing: 2px;
    border-radius: 20px;
    padding: 20px;
    animation: anim_texte 1s linear 0s 1 normal forwards;
}

a.close {
    color: #b97629;
    position: relative;
    font-size: 2rem;
    cursor: pointer;
    &:hover {
        color: rgb(102, 10, 10);
    }

    text-align: right;
}

.button {
    background: $white;
    color: $black;
    border: none;
    border-radius: 5px;

    padding: 5px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 1rem;
    cursor: pointer;
    &:hover {
        background: $black;
        color: $white;
    }
}

@keyframes anim_texte {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
