.socialnetwork-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 25px;
    right: 25px;
    padding-left: 10px;

    z-index: 1000;
    background-color: rgba(221, 95, 95, 0.74);

    height: 50px;
    width: 100px;

    .ico {
        font-size: 1.5rem;

        transition: 0.3s;
        color: $white;
    }
    .ico:hover {
        color: $green;
    }
}
